.MineDark{
    font-family: sans-serif;
    text-align: center;
  }
  
.row{
  display: flex;
}
  
.square{
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 3px solid #000000;
  font-size: 20px;

  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.3;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.revealed{
  opacity: 100%;
}